.Alumno {
  margin: 10px 40px 10px 40px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #3d0041;
  border-radius: 5px;
  transition: all 6s linear;
  position: relative;
}
.Imagen {
  display: none;
}
.Datos {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.Alumno > .Imagen {
  width: 75px;
  height: 75px;
  background-size: cover;
  text-align: center;
  align-items: center;
  position: relative;
  border-radius: 20%;
  background-color: #330036;
  border-radius: 20%;
}
.Alumno.activo {
  height: max-content;
  transition: all 2s ease-in-out;
}
.Alumno.activo > .boton {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 15px;
  margin-right: 10px;
}
.boton {
  border: none;
  cursor: pointer;
  background-color: #330036;
  width: 20px;
  height: 10px;
  border-radius: 5px;
  display: flex;
  place-content: center;
  align-items: center;
}
.boton > .linea {
  display: flex;
  background-color: #ffffff;
  width: 100%;
  height: 2px;
  border-radius: 2px;
}
.boton:hover {
  outline: 1px solid #ffffff;
}
.Alumno.activo > .Imagen {
  display: flex;
  background-color: #330036;
}

.Alumno.activo > .Datos {
  width: 50%;
  flex-direction: column;
  align-items: flex-start;
}
.Alumno > .Imagen.cargando {
  background-color: #3d0041;
  overflow: hidden;
}
.Alumno > .Imagen.cargando::after {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #330036;
  border-radius: 20%;
  transform: translate(-100%);
  animation: cargando 1s linear infinite;
}

.Alumno > .Imagen.cargado {
  background-image: url("../public/alumno.png");
}

@keyframes cargando {
  0% {
    background-color: #3d0041;
    transform: translate(-150, -150);
  }
  100% {
    transform: translate(150%);
    background-color: #330036;
  }
}
